<template>
  <v-card>
    <v-card-title class="subheading font-weight-bold">
      <v-checkbox v-model="checked"></v-checkbox>
      <span :class="{ crossout: checked }">{{ item.Name }}</span>
      <img class="image" :src="item.Image" />
    </v-card-title>

    <v-divider></v-divider>

    <table class="details">
      <tr>
        <td>Price</td>
        <td>{{ displayPrice(item.Price) }}</td>
      </tr>
      <tr>
        <td>Location</td>
        <td>{{ displayLocation(item.Location) }}</td>
      </tr>
      <tr>
        <td>Time</td>
        <td>
          <time-range :hours="item.hours" :readOnly="true"></time-range>
        </td>
      </tr>
      <tr>
        <td>Months</td>
        <td>
          <month-range :months="item.months" :readOnly="true"></month-range>
        </td>
      </tr>
    </table>
  </v-card>
</template>

<script>
import TimeRange from "@/components/TimeRange.vue";
import MonthRange from "@/components/MonthRange.vue";

export default {
  components: {
    TimeRange,
    MonthRange
  },

  props: {
    item: {
      type: Object
    }
  },

  data: function() {
    return {
      checked: false
    };
  },

  methods: {
    numberWithCommas(number) {
      return number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },

    displayPrice(price) {
      if (price == -1) {
        return "Unknown";
      } else {
        return this.numberWithCommas(price) + " bells";
      }
    },

    displayLocation(location) {
      if (location == "?") {
        return "Unknown";
      } else {
        return location;
      }
    }
  },

  mounted() {
    if (localStorage[this.item.Name]) {
      this.checked = true;
    }
  },

  watch: {
    checked(update) {
      if (update) {
        localStorage[this.item.Name] = true;
      } else {
        delete localStorage[this.item.Name];
      }
    }
  }
};
</script>

<style>
.image {
  height: 32px;
  margin-left: 8px;
}

.details {
  width: 100%;
  padding: 16px;
  margin-left: auto;
  margin-right: auto;
}

.details td {
  padding-top: 0.5em;
  padding-right: 1em;
}

.crossout {
  text-decoration: line-through;
}
</style>