<template>
  <v-col>
    <v-row>
      <!-- from 1 to 12, inclusive -->
      <span
        v-for="n in 12"
        :key="n"
        class="month north"
        v-bind:class="{ 'off': !values[n - 1] }"
        v-on:click="toggle(n - 1)"
      >{{ labels[n - 1] }}</span>
    </v-row>
    <v-row>
      <span
        v-for="n in 12"
        :key="n"
        class="month south"
        v-bind:class="{ 'off': !values[n + 11] }"
        v-on:click="toggle(n + 11)"
      >{{ labels[n - 1] }}</span>
    </v-row>
  </v-col>
</template>

<script>
import Vue from "vue";

export default {
  name: "MonthRange",

  props: {
    months: {
      type: Array,
      default: function() {
        return new Array(24);
      }
    },
    readOnly: {
      type: Boolean,
      default: false
    }
  },

  data: function() {
    return {
      labels: ["J", "F", "M", "A", "M", "J", "J", "A", "S", "O", "N", "D"],
      values: []
    };
  },

  methods: {
    toggle(index) {
      if (this.readOnly) return;

      let newValue = !this.values[index];
      Vue.set(this.values, index, newValue);
      this.$emit("update:values", this.values);
    }
  },

  created() {
    this.values = this.months;

    for (var i = 0; i < this.values.length; i++) {
      if (this.values[i] == undefined) {
        this.values[i] = false;
      }
    }
  }
};
</script>

<style>
.month {
  height: 2.5em;
  width: 1.75em;
  line-height: 2.5em;
  text-align: center;
  cursor: default;
}

.month.north {
  background-color: #2e63a7;
}

.month.south {
  background-color: #fe8035;
}

.off {
  background-color: rgba(255, 255, 255, 0.1) !important;
}
</style>