<template>
  <v-app id="inspire">
    <v-navigation-drawer v-model="drawer" app clipped>
      <v-list dense>
        <v-list-item link v-on:click="category = 'bugs'">
          <v-list-item-action>
            <v-icon>mdi-ladybug</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Bugs</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list-item link v-on:click="category = 'fish'">
          <v-list-item-action>
            <v-icon>mdi-fish</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Fish</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list-item link v-on:click="category = 'fossils'">
          <v-list-item-action>
            <v-icon>mdi-google-downasaur</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Fossils</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar app clipped-left>
      <v-app-bar-nav-icon @click.stop="drawer = !drawer" />
      <v-toolbar-title>Critterpedia</v-toolbar-title>
    </v-app-bar>

    <v-content>
      <v-container fluid>
        <v-data-iterator
          :items="filteredItems"
          :disable-pagination="true"
          :search="search"
          :sort-by="sortBy"
          :sort-desc="sortDesc"
          hide-default-footer
        >
          <template v-slot:header>
            <v-card color="blue darken-3" class="mb-1">
              <v-container fluid dark>
                <v-row align="center">
                  <v-col cols="12" sm="5" md="3" lg="2">
                    <v-text-field
                      v-model="search"
                      prepend-inner-icon="mdi-magnify"
                      label="Search"
                      clearable
                      flat
                      solo-inverted
                      hide-details
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="5" md="3" lg="2">
                    <v-select
                      v-model="sortBy"
                      :items="keys"
                      prepend-inner-icon="mdi-sort"
                      label="Sort by"
                      flat
                      solo-inverted
                      hide-details
                    ></v-select>
                  </v-col>
                  <v-col cols="12" sm="4" md="2" lg="1">
                    <v-btn-toggle v-model="sortDesc" mandatory>
                      <v-btn large depressed color="blue" :value="false">
                        <v-icon>mdi-arrow-up</v-icon>
                      </v-btn>
                      <v-btn large depressed color="blue" :value="true">
                        <v-icon>mdi-arrow-down</v-icon>
                      </v-btn>
                    </v-btn-toggle>
                  </v-col>
                  <v-col cols="12" sm="5" md="3" lg="2">
                    <time-range v-bind:values.sync="filter.hours"></time-range>
                  </v-col>
                  <v-col cols="12" sm="5" md="3" lg="2">
                    <month-range v-bind:values.sync="filter.months"></month-range>
                  </v-col>
                </v-row>
              </v-container>
            </v-card>
          </template>

          <template v-slot:default="props">
            <v-row>
              <v-col v-for="item in props.items" :key="item.Name" cols="12" sm="6" md="4" lg="3">
                <critter-bug :item="item"></critter-bug>
              </v-col>
            </v-row>
          </template>
        </v-data-iterator>
      </v-container>
    </v-content>
    <v-footer app>
      <span>&copy; 2020 Augusto Blomer</span>
    </v-footer>
  </v-app>
</template>

<script>
import CritterBug from "@/components/CritterBug.vue";
import TimeRange from "@/components/TimeRange.vue";
import MonthRange from "@/components/MonthRange.vue";

export default {
  components: {
    CritterBug,
    TimeRange,
    MonthRange
  },

  props: {
    source: String
  },

  data: () => ({
    drawer: null,
    category: "bugs",
    items: {
      bugs: [],
      fish: [],
      fossils: []
    },
    search: "",
    sortDesc: false,
    sortBy: "Name",
    keys: ["Name", "Price", "Location"],
    filter: {
      hours: [],
      months: []
    }
  }),

  computed: {
    filteredItems: function() {
      let items = this.items[this.category];
      let filter = this.filter;
      let badItems = [];

      items.forEach(function(item) {
        for (var i = 0; i < 24; i++) {
          if (filter.hours[i] == true && filter.hours[i] != item.hours[i]) {
            badItems.push(item);
            return;
          }
        }

        for (var j = 0; j < 24; j++) {
          if (filter.months[j] == true && filter.months[j] != item.months[j]) {
            badItems.push(item);
            return;
          }
        }
      });

      return items.filter(x => !badItems.includes(x));
    }
  },

  methods: {
    switchCategory: function(category) {
      this.category = category;
    }
  },

  created() {
    this.$vuetify.theme.dark = true;
  },

  mounted() {
    let self = this;
    var bugsClient = new XMLHttpRequest();
    bugsClient.open("GET", "data/bugs.json");

    bugsClient.onload = function() {
      let data = bugsClient.responseText;
      let bugs = JSON.parse(data);

      bugs.forEach(function(item) {
        item.hours = hoursArray(item);
        item.months = monthsArray(item);
      });

      self.items.bugs = bugs;
    };

    bugsClient.send();

    var fishClient = new XMLHttpRequest();
    fishClient.open("GET", "data/fish.json");

    fishClient.onload = function() {
      let data = fishClient.responseText;
      let fish = JSON.parse(data);

      fish.forEach(function(item) {
        item.hours = hoursArray(item);
        item.months = monthsArray(item);
      });

      self.items.fish = fish;
    };

    fishClient.send();
  }
};

function hoursArray(item) {
  return [
    item.hour0 == 1,
    item.hour1 == 1,
    item.hour2 == 1,
    item.hour3 == 1,
    item.hour4 == 1,
    item.hour5 == 1,
    item.hour6 == 1,
    item.hour7 == 1,
    item.hour8 == 1,
    item.hour9 == 1,
    item.hour10 == 1,
    item.hour11 == 1,
    item.hour12 == 1,
    item.hour13 == 1,
    item.hour14 == 1,
    item.hour15 == 1,
    item.hour16 == 1,
    item.hour17 == 1,
    item.hour18 == 1,
    item.hour19 == 1,
    item.hour20 == 1,
    item.hour21 == 1,
    item.hour22 == 1,
    item.hour23 == 1
  ];
}

function monthsArray(item) {
  return [
    item.Jan == 1,
    item.Feb == 1,
    item.Mar == 1,
    item.Apr == 1,
    item.May == 1,
    item.Jun == 1,
    item.Jul == 1,
    item.Aug == 1,
    item.Sep == 1,
    item.Oct == 1,
    item.Nov == 1,
    item.Dec == 1,
    item.JanS == 1,
    item.FebS == 1,
    item.MarS == 1,
    item.AprS == 1,
    item.MayS == 1,
    item.JunS == 1,
    item.JulS == 1,
    item.AugS == 1,
    item.SepS == 1,
    item.OctS == 1,
    item.NovS == 1,
    item.DecS == 1
  ];
}
</script>

<style>
.filter {
  padding: 16px;
}
</style>